import React, { Component } from "react";
import { Grid, Typography, CssBaseline } from "@material-ui/core";
import Typewriter from "./typewriter";
import Scrolldown from "./Scrolldown";

class CoverPage extends Component {
  render() {
    const description = [
      "Application Developer",
      "Loves programming"
    ];
    return (
      <Grid
        container
        direction="column"
        justify="center"
        alignItems="center"
        style={{
          height: "100vh",
          background:
            "linear-gradient(13deg, rgba(132,14,161,1) 24%, rgba(29,142,143,1) 83%)"
        }}
      >
        <CssBaseline />
        <Grid container item justify="center">
          <Typography align="center" component="div" style={{color: "powderblue", border: "4px solid white", 
        fontSize: "2rem", padding: "10px"}}>
          I'm Karan Chauhan <br/>
          <div style={{fontSize: "1rem"}}>Full-Stack Application Developer</div>
        </Typography>
        </Grid>
        <Grid container item justify="center">
          <Typography style={{padding: "40px", color: "antiquewhite"}}>
            <br/>
            <Typewriter data={description} loop={true} speed={200}/>
          </Typography>
        </Grid>
        <Grid container item justify="center" style={{bottom: "100px", position: "absolute"}}>
          <Scrolldown/>
        </Grid>
      </Grid>
    );
  }
}

export default CoverPage;
