import React, { Component } from "react";
import { Grid, Typography } from "@material-ui/core";
import ProjectCard from "./ProjectCard";
import CreateIcon from "@material-ui/icons/Create";
import PublicIcon from "@material-ui/icons/Public";
import SportsEsportsIcon from "@material-ui/icons/SportsEsports";

class Projects extends Component {
  render() {
    const projectData = [
      {
        name: "Notes Application",
        type: "Web Application",
        url: "https://notes-keepitsimple.herokuapp.com/",
        description:
          "This is an application where can users can store daily notes.",
        stack: ["Java", "Spring Boot", "MongoDB", "React", "JavaScript"],
      },
      {
        name: "Youngsters",
        type: "Web Application",
        url: "http://youngsters.chauh11r.myweb.cs.uwindsor.ca/",
        description:
          "Youngsters is a social network where users can make new friends and interact with them.",
        stack: ["PHP", "MYSQL", "JavaScript"],
      },
      {
        name: "Zombie Kill Game",
        type: "Desktop Application",
        url: "https://youtu.be/SIia_VAAYfo",
        description:
          "Shooting game where main aim is to kill zombies and main boss. This includes power-ups.",
        stack: ["Python", "PyGame"],
      },
    ];
    return (
      <Grid style={{ padding: "20px" }}>
        <Grid container justify="center" item xs={12}>
          <Typography variant="h5" style={{ color: "black" }}>
            Projects
          </Typography>
        </Grid>
        <Grid container direction="row" justify="center" item xs={12} spacing={2}>
          <Grid  item>
            <ProjectCard icon={<CreateIcon />} data={projectData[0]} />
          </Grid>
          <Grid  item>
            <ProjectCard icon={<PublicIcon />} data={projectData[1]} />
          </Grid>
          <Grid  item>
            <ProjectCard icon={<SportsEsportsIcon />} data={projectData[2]} />
          </Grid>
        </Grid>
      </Grid>
    );
  }
}

export default Projects;
