import React, { Component } from "react";
import { Grid, Typography, Link } from "@material-ui/core";
import FavoriteIcon from '@material-ui/icons/Favorite';

export default class Footer extends Component {
  render() {
    return (
      <Grid
        container
        justify="center"
        alignItems="center"
        style={{ padding: "20px", backgroundColor: "darkslategray", color: "white" }}
      >
        <Grid item>
          <Typography align="center">
            Made with <FavoriteIcon style={{ color: "red"}} /> using&nbsp;
            <Link target="_blank"
              rel="noopener noreferrer" href="https://reactjs.org/" style={{ color: "white"}}>
              React
            </Link>
            &nbsp;and&nbsp;
            <Link target="_blank"
              rel="noopener noreferrer" href="https://material-ui.com/" style={{ color: "white"}}>
              Material UI
            </Link>
            <br/>
            A&nbsp;
            <Link target="_blank"
              rel="noopener noreferrer" href="https://github.com/itsmekaran" style={{ color: "white"}}>
              Karan Chauhan's
            </Link>
            &nbsp;Production 2020
          </Typography>
        </Grid>
      </Grid>
    );
  }
}
