import React, { Component } from "react";
import { Grid, Typography, IconButton } from "@material-ui/core";
import Badge from "./linkedinbadge";
import GitHubIcon from '@material-ui/icons/GitHub';

class About extends Component {
  render() {
    return (
      <Grid style={{ backgroundColor: "darkslategrey", padding: "20px"}}>
        <Grid container justify="center" item xs={12}>
          <Typography
            variant="h5"
            style={{ color: "aliceblue" }}
          >
            About me
          </Typography>
        </Grid>
        <Grid container item>
          <Grid
            item
            container
            justify="center"
            alignItems="center"
            xs={12}
            md={6}
          >
            <Badge />
          </Grid>
          <Grid
            item
            container
            justify="center"
            alignItems="center"
            xs={12}
            md={6}
          >
            <Typography
            variant="body1"
            style={{ color: "white", fontSize: "large" }}
          >
            <b>Karan Chauhan</b> <br/><br/>
            I am a full-stack application developer and have 3 years of hands-on experience in developing applications using 
            different technology stacks. Although I consider myself as a full-stack developer, I do focus 
            on backend or server side technologies more than frontend technologies.<br/><br/>
            I love solving data structures and algorithm problems. 
          </Typography>
          <IconButton aria-label="link">
            <a
              target="_blank"
              rel="noopener noreferrer"
              href="https://github.com/itsmekaran"
            >
              <GitHubIcon style={{color: "cyan"}} />
            </a>
          </IconButton>
          </Grid>
        </Grid>
      </Grid>
    );
  }
}

export default About;
