import React, { Component } from "react";

class Typewriter extends Component {
  constructor(props) {
    super(props);
    this.state = {
      element: "",
      textIndex: 0,
    };
    this.createEffect();
    this.resetEffect = this.resetEffect.bind(this);
  }

  resetEffect(textIndex) {
    this.setState(
      {
        element: "",
        textIndex: textIndex,
      },
      () => {
        this.createEffect();
      }
    );
  }

  createEffect() {
    let charArray = this.props.data[this.state.textIndex].split("");
    let index = 0;
    let interval = setInterval(() => {
      this.setState((prevState) => ({
        element: prevState.element.slice(0, -1) + charArray[index] + "_",
      }));
      index++;
      if (index > charArray.length) {
        this.setState(
          (prevState) => ({
            element: prevState.element.slice(0, -1),
          }),
          () => {
            clearInterval(interval);
            if (this.props.loop) {
              let nextIndex = this.state.textIndex + 1;
              if (nextIndex >= this.props.data.length) {
                nextIndex = 0;
              }
              this.resetEffect(nextIndex);
            }
          }
        );
      }
    }, this.props.speed);
  }

  render() {
    return <>{this.state.element}</>;
  }
}

export default Typewriter;
